import { TYPES } from "../constants"
import { entityIdToColor, eventIdToColor } from "./detection-id-to-color"

export type HighlightItem = {
  className: string
  style?: object
  color?: string
  rpJsonId?: string
  rpJsonType?: string
  highlight?: boolean
}

const getStyle = (item: HighlightItem) => {
  if (item.style) return item.style
  if (item.rpJsonType === TYPES.EVENT) {
    const color = item.color || eventIdToColor(item.rpJsonId)
    return {
      borderTop: item.highlight ? `2px solid ${color}` : "none",
      borderBottom: item.highlight ? `2px solid ${color}` : "none",
    }
  }
  return {
    backgroundColor: item.highlight ? item.color || entityIdToColor(item.rpJsonId) : "none",
  }
}

export const generateRpJsonStylesheet = (highlights: Array<HighlightItem>) => {
  return highlights.reduce((acc, item) => {
    return { ...acc, [item.className]: getStyle(item) }
  }, {})
}
